.App {
  text-align: center;
  
}


.App-header {
  background-image: url(./assets/blob.svg);
  background-size: cover;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.holder{ 
  width: 65vw;
  min-height: 30vh;
  background-color: rgba(0, 0, 0, 0.486);
  text-align: center;
  border-radius: 3vh 3vh 3vh 3vh;
  padding-bottom: 5vh;
}
.buttonHolder{
  flex-direction:row;
  justify-content: space-between;
  }
.btn{
  margin-left: 1vh;
  padding: 0vh 3vh 0vh 3vh;
  border-radius: 3vh 3vh 3vh 3vh;
  box-shadow: black, 1vh 1vh;
  background-color: rgba(0, 0, 0, 0.747);
  color: whitesmoke;
  min-width: 10%;
  height: 5%;
}
.btn2{
  margin-left: 1vh;
  min-width: 10%;
  height: 5%;
  border-radius: 3vh 3vh 3vh 3vh;
  box-shadow: black, 1vh 1vh;
  background-color: rgba(0, 0, 0, 0.747);
  color: whitesmoke;
  
}

.floatDown{
  position: absolute;bottom:0;
  max-height: 10vh;
  text-align: center;
  color: black;
  margin: 0vh 2vh 0vh 2vh;
} 
